import React from "react";
import Typed from "react-typed";

const Header = () => {
    return (
        <div id="section1">
        <div className="header-wraper">
            <div className="main-info">
                <h1> Hii, I am Deshan Vimukthi.</h1>
                <h2><Typed
                    className="typed-text"
                    strings={["Web Designer", "Frontend Developer", "Backend Developer", ]}
                    typeSpeed={40}
                    backSpeed={60}
                    loop
                  /> 
                </h2>
                <button type="button" className="btn btn-outline-secondary">
                    <p><a href="https://drive.google.com/file/d/1axOwrr8Pfi2OJ-Dp1BiZmx1vRZnHbph0/view?usp=sharing"
                     className="btn btn-primary btn-learn">Download CV <em className="icon-download4"></em>
                     </a>
                     </p>
                </button>
            </div>
            
         </div>
         </div>
    )
}

export default Header
