import React from 'react';
//REACT FONTAWESOME IMPORTS
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars} from "@fortawesome/free-solid-svg-icons";


const NavBar = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <a className="navbar-brand" href="#section1">DeshanVimukthi</a>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <FontAwesomeIcon icon={faBars} style= {{color:"black"}} />
        </button>
      
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item active">
              <a className="nav-link" href="#section1">Home <span className="sr-only">(current)</span></a>
            </li>
            <li className="nav-item">
              <a  className="nav-link" href="#section2" >About me</a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#section3">Project</a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#section5">Skills</a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#footer">Contact</a>
            </li>
    
          </ul>
       
        </div>
      </nav>
    )
}

export default NavBar


