import React from 'react';
// npm install --save-dev @iconify/react @iconify-icons/logos
import { Icon } from "@iconify/react";
import reactIcon from "@iconify/icons-logos/react";
import javascriptIcon from '@iconify-icons/logos/javascript';
import html5 from '@iconify-icons/logos/html-5';
import javaIcon from '@iconify-icons/logos/java';
import nodejsIcon from '@iconify-icons/logos/nodejs';
import pythonIcon from '@iconify-icons/logos/python';
import css3 from '@iconify-icons/logos/css-3';
import mysqlIcon from '@iconify-icons/simple-icons/mysql';
import djangoIcon from '@iconify-icons/simple-icons/django';

const Skills = () => {
    return (
        <div id="section5">
            <div className="about-section1">
                <div className="container">
                    <div className="about">
                        <h2 className="about-title1">Here are some of my Programming Skills</h2>

                        <Icon
                            icon={mysqlIcon}
                            style={{ fontSize: "400%", margin: "9% 5% 10% 5%" }}
                        />
                        <Icon
                            icon={reactIcon}
                            style={{ fontSize: "400%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={javascriptIcon}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={html5}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={css3}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={javaIcon}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={pythonIcon}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={nodejsIcon}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                        <Icon
                            icon={djangoIcon}
                            style={{ fontSize: "600%", margin: "9% 5% 10% 5%" }}
                        />

                    </div>
                </div>
            </div>
        </div>

    )
}

export default Skills
